import React, { useContext, useEffect, useState } from 'react'
import { AuthContext } from '../../auth/authContext';

import './estilos-home-admin.css'

import homeImg from '../../images/home.svg'
import { getSolicitudPendiente } from '../../service/apiSolicitudAulas';

export const AdministradorScreen = () => {
    
    const { user } = useContext(AuthContext);

    const [solicitudesPendientes, setSolicitudesPendientes] = useState({
        state: false,
        data : []
    })

    const { data } = solicitudesPendientes;

    useEffect(() => {
        
        getSolicitudPendiente(setSolicitudesPendientes);

    }, [])
    

    return (
        <div className='contenedor-home-admin animate__animated animate__fadeIn'>
            <h2>Hola, {user.name} {user.apellido}!</h2>
            <hr/>
            <div className='contenedor-info-admin'>
                <section>
                    <div>
                        <div>
                            <p>Atencin! Tienes <b>{data.length}</b> solicitudes de reserva de aulas pendientes! Dirgete a la seccin <b>Solicitudes</b> para poder atenderlas!</p>
                            <hr/>
                            <p>Para poder ver las aulas disponibles en la plataforma, debes dirigirte a la seccin <b>Aulas.</b></p>
                            <p>Si deseas ver las materias y sus respectivos grupos disponibles en la plataforma, debes dirigirte a la seccin <b>Materias.</b></p>
                            <p>Para poder registrar una nueva materia, debes dirigirte a la seccin <b>Registrar Materia.</b></p>
                        </div>
                        <div className='contenedor-img-home-admin'>
                            <img className='img-home-admin' src={ homeImg } alt='home img'/>
                        </div>
                    </div>
                </section>
            </div>
        </div>
    )
}
